/* ------------------------------------------------------------------------------------------------------------------------------
PAGE MAIN CATEGORIES SECTION
--------------------------------------------------------------------------------------------------------------------------------*/
.main-categories {
	margin-bottom: 40px;

	&-card {
		display: flex;
		gap: 15px;

        &:hover{
            img {
				transform: scale(1.1);
			}		
            .main-categories-card_title {
				color: var(--accent);
			}	
			.main-categories-card_description{
				color: var(--accent);
			}
        }

		&_image {
			aspect-ratio: 1/1;
			width: 60px;
			flex-shrink: 0;
			overflow: hidden;

			border-radius: 5px;

			img {
				display: block;
				width: 100%;
				height: auto;
				object-fit: cover;
				overflow: hidden;

				transition: all 0.3s ease-in-out;
			}
		}

		&_text {
			flex-grow: 1;
		}

		&_title {
			font-family: var(--font-title);
			font-weight: 500;
			font-size: 18px;
			color: var(--black);

            transition: color 0.3s ease-in-out;

            margin-bottom: 5px;
		}

		&_description {		            
			font-weight: 400;
			font-size: 14px;
			line-height: 120%;
			color: #b1b1b1;      
            
            transition: color 0.3s ease-in-out;

            max-width: 85%;	
		}
	}
}
